import { ObservableMap, observable, action,toJS} from 'mobx';
const ApiRoot = `https://pokeapi.co/api/v2`


export default class Pokemons {
    pokemonsArr = observable([]);
    @observable page = 0;
    @observable rowsPerPage = 10;
    @action setPage = (newPage) => {
        this.page = newPage;
    };
    @action setRowsPerPage = (newPerPage) => {
        this.rowsPerPage = newPerPage;
    };

    @action getPockemons = async() => {
        let resp =  await fetch(`${ApiRoot}/pokemon/?offset=0&limit=50`);
        let response = await resp.json();
        this.pokemonsArr.replace(toJS(response.results));
    };

    @action getTypes = async() => {
        let resp =  await fetch(`${ApiRoot}/type/?offset=0&limit=50`);
        let response = await resp.json();
        this.pokemonsArr = observable(response.results);
    }

}
